/**
 * This function Show the alert of the ajax petition
 */
function showAlarms(color = "", icon = "", messageUser = "", messageError = "") {

    $('#colorMessage').val(color);
    $('#iconMessage').val(icon);
    $('#messageNotif').val(messageUser);

    $('#btnNotification').click();

    let tipoNotification;
    switch (color) {
        case 1:
            tipoNotification = "Aviso";
            break;

        case 2:
            tipoNotification = "Éxito";
            break;

        case 3:
            tipoNotification = "Advertencia";
            break;

        case 4:
            tipoNotification = "Error";
            break;
    }

    // registrarNotificacion(messageUser, messageError, tipoNotification);
}

